<template>
  <div class="px-9 <lg:px-4 border-b-1 border-color py-4 text-sm font-bold">
    <div class="text-2xl font-light">
      {{ $store.state.auth.me?.login }}
    </div>

    <div class="text-sm text-fg-secondary-light font-light">
      Управление вашим профилем Money
    </div>
  </div>

  <div class="mx-9 <lg:mx-4 mt-8">
    <div class="mb-8 text-fg-light dark:text-fg font-medium text-xl">
      Смена пароля

      <div class="text-sm mt-4">
        Старый пароль
      </div>
      <UiInput
        v-model="password"
        type="password"
        class="block text-sm h-10 font-medium mt-2 w-70"/>

      <div class="text-sm mt-3">
        Новый пароль
      </div>
      <UiInput
        v-model="password"
        type="password"
        class="block text-sm h-10 font-medium mt-2 w-70"/>

      <div class="text-sm mt-3">
        Повторите новый пароль
      </div>
      <UiInput
        v-model="password"
        type="password"
        class="block text-sm h-10 font-medium mt-2 w-70"/>

      <div class="text-sm text-fg-secondary-light mt-3 max-w-130">
        Убедитесь, что в пароле не менее 15 символов ИЛИ не менее 8 символов,
        включая цифру и строчную букву
      </div>

      <div class="flex flex-wrap gap-5 items-center mt-5">
        <UiButton class="text-sm py-1 px-3">
          Обновить пароль
        </UiButton>

        <router-link to="/" class="text-sm underline">
          Я забыл(а) пароль
        </router-link>
      </div>
    </div>

    <div class="mb-4 text-fg-light dark:text-fg font-medium text-xl">
      Опасная зона
    </div>

    <div class="flex flex-wrap gap-8 mb-8">
      <div class="max-w-94 rounded border-color border-1 p-4">
        <div class="text-fg-light dark:text-fg text-tiny font-medium mb-2">
          Удаление всех данных
        </div>

        <div class="text-fg-secondary-light text-sm font-medium mb-4">
          После удаления всех данных об операциях и счетов, пути назад уже не будет.
          Пожалуйста, будьте уверены.
        </div>

        <UiButton
          @click="isConfirmingDelete = true"
          type="danger"
          class="p-2 rounded transition-all border-1 py-1
            text-sm
            bg-btn-light hover:bg-btn-light-hover
            dark:bg-btn dark:hover:bg-btn-hover"
        >
          <div
            class="flex gap-2 items-center"
          >
            <TrashIcon class="w-4 h-4"/>
            <div>
              Удалить все операции
            </div>
          </div>
        </UiButton>
      </div>

      <div class="max-w-94 rounded border-color border-1 p-4">
        <div class="text-fg-light dark:text-fg text-tiny font-medium mb-2">
          Удаление аккаунта
        </div>

        <div class="text-fg-secondary-light text-sm font-medium mb-4">
          После удаления вашего аккаунта, пути назад уже не будет. Пожалуйста, будьте уверены.
        </div>

        <UiButton
          @click="isConfirmingDelete = true"
          type="danger"
          class="p-2 rounded transition-all border-1 py-1
            text-sm
            bg-btn-light hover:bg-btn-light-hover
            dark:bg-btn dark:hover:bg-btn-hover"
        >
          <div
            class="flex gap-2 items-center"
          >
            <TrashIcon class="w-4 h-4"/>
            <div>
              Удалить аккаунт
            </div>
          </div>
        </UiButton>
      </div>
    </div>

    <Modal
      :visible="isConfirmingDelete"
      @confirmation="handleConfirmationDelete"
    />

    <Alert v-model="serverError"/>
  </div>
</template>

<script>
import { TrashIcon } from '@heroicons/vue/solid';
import UiButton from '@/components/ui/Button.vue';
import UiInput from '@/components/ui/Input.vue';
import Modal from '@/components/Modal.vue';
import Alert from '@/components/Alert.vue';

import api from '@/api/index';

export default {
  components: {
    TrashIcon,
    Modal,
    Alert,
    UiButton,
    UiInput,
  },
  data() {
    return {
      isConfirmingDelete: false,
      serverError: '',
      password: '',
    };
  },
  methods: {
    handleConfirmationDelete(isConfirmed) {
      if (!isConfirmed) {
        this.isConfirmingDelete = false;
      } else {
        const { auth } = this.$store.state.auth;

        const handleError = (msg) => {
          console.log('Error', msg);
          this.serverError = msg;
          this.isConfirmingDelete = false;
        };

        api.removeMe(auth)
          .then((res) => {
            if (res.status === 'ok') {
              this.$store.dispatch('auth/logout')
                .then(() => {
                  this.$router.push('/login');
                })
                .catch((err) => handleError(err.message));
            } else {
              handleError(res.data);
            }
          })
          .catch((err) => handleError(err.message));
      }
    },
  },
};
</script>
